%btn{
    display          : inline-block;
    padding          : 0.5em 0.9em; 
    text-decoration  : none;
    border-radius    : 18px; 
    font-size        : 14px; 
  }
  @mixin wphButton($bg-color: rgb(93, 132, 218),$front-color: rgb(78, 74, 74),$box-color:rgb(107, 38, 148),$style: default) {
    @extend    %btn;
    background : $bg-color;
    color      : $front-color; 
    box-shadow : 2px 3px  $box-color;
  }