//Profile
@keyframes animationOpacity{
    from{opacity: 0;transform: rotateX(0deg)}
    to{opacity: 1;transform: rotateX(380deg)}
}
.profile{
   position: absolute;
   top: 18%; 
   left: 50%;
   transform: translateX(-50%);
   width: 52em;
   height: 31em;
   background-color: rgb(255, 255, 255);  
   display: flex;
   flex-flow: row wrap;
   margin-right: 0;   
   
   @include to(795px){
       width: 48em; 
       height: 32em;
   }
   @include to(45em){
       @include grid('.profile-container',1,0);  
       margin-top: -5em;  
       height: auto; 
       width: 80%;   
       
       .profile-content{
           margin-top: -8em;
           height:auto;  
           @include to(720px){
            margin-top: 0;   
           }
       }
       .profile-socialmedia{ 
           &__link{
             margin: 0 .7em;
           }
       }
   }
   @include to(35em){ 
       width: 90%; 
   }
   @include to(25em){ 
       width: 96%; 
   } 
   &-avatar{ 
     display: flex;
     justify-content: center;
     align-items: center; 
     width: 30%; 
     background-color:#ffffff;  
     position: relative;
     @include to(720px){
        height: 130px;
     }
     @include to(420px){
        height: 100px;

     }
     &::before{
        content: '';
        position: absolute;
        width: 92px;
        height: 92px;
        border: 3px solid var(--bg-color1);
        z-index: 5;
        border-radius: 50%;  
        @include to(420px){
            width: 72px;
            height: 72px;
         }
    }
     img{
        width: 80px;
        height: 80px;
        object-fit: cover;
        object-position: center;
        border-radius: 50%; 
        display: block;
        @include to(420px){
            width: 60px;
            height: 60px;
         }
     }
   } 
   &-content{
       width: 70%;
       background-color:#ffffff;
       padding: 0 2em;
       color: $color-opacity2;
       @include to(335px){ 
           padding: 0 1em;
       }
       @include to(300px){ 
           padding: 1em 0.5em;
       }
       @include to(260px){ 
           padding: 3em 0.5em;
       }
       @include to(225px){ 
           padding: 4em 0.5em;
       }
       &__hello{ 
          position: relative;
          width: 6em; 
          background-color: var(--bg-color1);
          color: #ffffff;
          text-align: center;
          @include to(335px){ 
           margin-top: .5em;
           } 
          & p{
              padding: 4px;
              text-transform: uppercase;
              @include to(270px){ 
               padding: 4px 1px; 
             }
          }
          &::before{
              content:'';
              position: absolute; 
              left:1px;
              bottom: -10px;   
              margin-left: -5px;
              border-width: 15px;
              border-style: solid;
              border-color:var(--bg-color1) transparent transparent transparent;
              transform: rotate(133deg);
            
          }
       }
       &__name{
           font-weight: normal; 
           @include to(400px){ 
               font-size: 14px;
           }
           & span{
              font-weight: bold;
              font-size: 1.5em;
           }
       }
       &__line-space{
          display: block;
          border-bottom: thin solid $color-opacity1; 
       }  
       &-table{
           margin-top: 13px;
           margin-left: 0;
           padding-left: 0;
           border-collapse: collapse;
           text-align: left;
           @include to(400px){ 
                    display: none; 
            }
           & th{
               padding: 0 20px 0 0;
               
           }
           th:nth-child(2){
               font-weight: normal; 
           }
       }

   }
   &-socialmedia{
       display: flex;
       justify-content: center; 
       align-items: center; 
       width: 100%;
       background-color:var(--bg-color1);
       @include to(720px){
           padding: .6em; 
        }
       @include to(400px){
          :nth-child(4),:nth-child(5){
               display: none;
           }
       }
       &__link{
           display: inherit;
           justify-content: center;
           align-items: center;
           margin:0 1em;
           width: 45px;
           height: 45px;
           border-radius: 50%; 
           text-decoration: none; 
           color: $color-text-white; 
           background-color:var(--bg-color1);
           &:hover{
              @include animationAction;
           }
           &:active{
               transform: scale(0.8);
           }
       }
       & .facebook:hover{
           background-color: #405D9A;
       } 
       & .github:hover{
           background-color: #000000; 
       } 
       & .twitter:hover{
           background-color: #1DA1F2;
       } 
       & .whatsapp{
           position: relative;
       }
        
       & .whatsapp:hover{
           background-color:#00E676;

       } 
       & .linkedin:hover{
           background-color: #0077B5;

       } 
     
   }
   
} 