
// Footer

.main-footer{ 
   
    // border-top: 5px solid rgba(0, 0, 0, 0.12);
    color: rgb(185, 180, 180);
    background-color: var(--bg-color1);
    // box-shadow: inset 0 1px 2px rgba(0,0,0,.80);  
    margin-top: 2em; 
    &-container{  
        padding:2em; 
        @include edContainer;
        justify-content: center; 
    }
} 
//end-footer 

.footer-end{ 
    display: block;
    position: relative;
    background-color: var(--bg-color1);
    color: #a8b0b8; 
    &::before{
        content: ''; 
        top: 0;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 80%;
        height: 1px;
        background: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0.01)), color-stop(0%, rgba(0, 0, 0, 0.01)), color-stop(50%, #a5a4a4), to(rgba(0, 0, 0, 0.01)));
     } 
    &::after{
        top: 0;
        content:'';
        position: absolute;
        width: 100%;
        height: 100%; 
    } 
  
    &__content{
        @include edContainer; 
        justify-content: center; 
        
    }
    &__description{ 
        text-align: center;
       
    }
    
}  




