
// font-size
:root{
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .8125rem;
    @include to(m){
        --h1-font-size: 1.5rem;
        --h2-font-size: 1.25rem;
        --h3-font-size: 1rem;
        --normal-font-size: .9375rem;
        --small-font-size: .875rem;
        --smaller-font-size: .75rem; 
    }
}
.title,.subtitle{
    color: #9c9898;
    font-weight: 600; 
    text-align: center; 
}
.title{  
    font-size: var(--h1-font-size);
    margin-top: 1em;
    
}
.subtitle{ 
    font-size: var(--h2-font-size);
    margin-top: 1.5em;
    
}


// Experience
.experience{  
    &__title{
        text-align: center;
        font-size: 2em;
        color: $color-opacity3; 
    }
}
// success
 
.success{width: 100%;
    @include to(78em){
        padding: 0 1em;
    }
    @include to(810px){ 
        display: flex;
        flex-flow: column nowrap; 
        align-items: center;
        width: 100%;
    }
    &__title{ 
        color: $color-opacity3;
        font-size: em(30px); 
        @include to(425px){
            text-align: center;
        }
    }
    &-container{
       display: flex;
       @include to(810px){
         display: block;
       }
       & .container-success-website{ 
           width: 380px;
           @include boxShadow;
           border-radius:8px;
           background-color: #ffffff;  
           margin-right: 1em;
           display: block; 
           @include to(420px){
            width: auto;
            margin:0;  
           }
           @include to(810px){
            margin:0;
            margin-bottom:1em;  
          }
            .container__line{
                border-bottom: 1px solid $color-text-dark1;    
            }
            .image-website{
                position: relative;
                overflow: hidden;
                width: 100%; 
                margin: 0; 
               
                &__photo{ 
                    border-radius: 8px  8px 0 0;
                    width: 100%;
                    height:auto;
                    object-fit: cover;
                    @include to(m){
                        height:auto;
                    } 
                }  
            }
            .content-website{  
                letter-spacing: .005em;
                padding: 1.5em;
                &__title{ 
                    color: $color-text-dark4; 
                    font-size:1.2em;
                    margin: 0.3em 0;
                }
                &__subtitle{
                    color: $color-text-dark1; 
                    margin: 0;
                    font-weight:lighter;
                    font-size: 14px;
                }
                &__description{ 
                    color: $color-text-dark4;
                    font-size: 15px;  

                }
                

            }
            .content-link{
                width: 100%;
                display: flex;
                padding: 1em 1em;
                justify-content:flex-end;
                @include to(320px){
                    display: flex;
                    flex-flow: row wrap;
                    justify-content:center;
                }
                &__btn { 
                    @include wphButton(var(--bg-color1),$color-text-white,var(--bg-color2));
                    margin-right: 1em; 
                    @include to(320px){   
                        margin-bottom: .5em;  
                        margin-right:.5em; 
                    } 
               
                }  
            }
       }
    }
}