//About 
.about{
    position: relative;
    background: rgb(31, 177, 235);
    width: 100%;
    height: 39rem; 
    overflow: hidden;
    @include to(45em){    
            height:55em;
    }  
    @include to(261px){      
        height:60em;
    }
    @include to(426px){    
        height:55em;
    } 
 
    &-container1{ 
        width: 100%;
        height: 30rem; 
        background:url(https://images.pexels.com/photos/356056/pexels-photo-356056.jpeg?w=940&h=650&auto=compress&cs=tinysrgb) no-repeat center/cover;
        &::before{  
            position: absolute;
            content: '';
            width: 100%;
            height: 100%; 
            background-color: rgba($bg-color3,0.88);
        }
     
    }
    &-container2{
        position: absolute;
        top: 0;
        width: 100%;
        height: 15rem;
        background-color: var(--bg-color1); 
        clip-path: polygon(0 0, 100% 0, 100% 60%,0 42%); 
        width: 100%;
        height: 36rem;
        // background:url(https://images.pexels.com/photos/288183/pexels-photo-288183.jpeg?h=350&auto=compress&cs=tinysrgb) no-repeat center/cover;
        &::before{ 
            position: absolute; 
            content: '';
            width: 100%;
            height: 100%;
            background-color: rgba(var(--bg-color1),0.88);
        }
    }
    &-container3{ 
        position: relative;
        margin-top: -10em;
        background-color: #EFEFEF;
        width: 100%;
        height: 403em;
        clip-path: circle(70% at 50% 51%); 
    }
}