/* Hash target */ 
:target{
    padding-top:3em; 
} 

//Nav  que incluye la section de logo y menu 
.nav-container{
    position:  fixed;
    top: 9px;
    right: 15px;
    width: 35px;
    height: 35px;
    border-radius: 50%; 
    z-index: var(--z-modal);
    @include showTo($bp-menu); 

}
.nav-container:active{
 background-color: rgba(0, 0, 0, 0.40); 

}
.nav-toggle{ 
    @include navToggle(15px,#ffffff); 
    top: 9.5px;
    right: -6px;
    @include showTo($bp-menu); 
}
.go-rigth{ 
    transition: all .3s;
    margin-left: 50%;
}