// Contact

.contact{
    @include to(78em){
        padding: 0 1em;
    }
    &__title { 
        font-size: 1.8em;
    text-align: center;
    color: $color-opacity3;  
    }
    &-container{  
        @include grid('.contact-content',2); 
        width: 100%; 
        @include to(45em){
            @include grid('.contact-content',1); 
         }  
        .contact-content{    
            background-color: #ffffff;
            @include boxShadow;  
            &__title{ 
                padding:0;
                margin: 0 0 2em 0;
                font-size: 1.5em;
                color: $color-opacity3;
                
            }
            &-message{  
                padding: 2em; 

               & input[type*="text"]{ 
                    width: 100%;
                    margin-bottom: 2em;
                    border: none;
                    outline: none;
                    font-size: 15px;
                    border-bottom: thin solid rgb(224, 221, 221);
                    padding-bottom: 1em;
                    &:focus{
                        border-bottom: 3px solid $bg-color1;   
                    }
                }
                & input[type*="text"]:nth-child(5){
                    padding-bottom: 6em; 
                }
                &__sent{
                    @include edgridButton;
                    color:#ffffff;
                    background-color: var(--bg-color1);
                    border-radius: 4px;
                   
                }
                
            }
            &-address{ 
                padding: 0 1em;
                color: $color-opacity3; 
                margin-bottom: 0.5em;
             & th{
                 padding: .5em;
            } 
          }
          &-map{ 
            &__api{
                width:100%;
                height:26em; 
                display: block; 
            }
          }
        }
    }
}