/*Header*/

.header{
    @include edContainer;
    @include full; 
    position: fixed;
    top: 0;
    left: 0;  
    z-index: var(--z-fixed);
    background-color:var(--bg-color1);
    height: 3.5em; 
    
    &-main{
        @include edContainer;
        @include full;  
        .logo{ 
            &__title{
                margin: .5em;
            }
            @include edItem(100,30,55);
            font-family: 'Courgette', cursive;
            color: $color-text-dark1;
            span{
                color: $color-text-white;
            }  
         }
        .nav{  
            @include edItem(100,70,45);
            @include edNav(left,$bp-menu);     
            @include mainCenter;  
            padding-right: 0;
            padding-left: 0;
            @include to($bp-menu){
                top:3.5em; 
                width: 50%;
                background-color:$bg-color3;
            }
            @include to(20em){ 
                width: 75%; 
            }
            .menu{   
                @include edMenu($bp-menu,edNav);   
                &-item{  
                    @include to($bp-menu){ 
                        border-bottom: 1px solid #d6d6d6; 
                        :hover{
                            background-color: rgba(0, 0, 0, 0.1);
                        }    
                    } 
                }
                &-link{
                    position: relative;
                   @include from($bp-menu){
                    &::after{
                        content:''; 
                        position: absolute;
                        left: 0; 
                        bottom: -10px; 
                        width: 0;
                        height: 4px;
                        background-color: #fff;
                        transition: width .2s linear;
                       
                    }  
                    &:hover.menu-link::after{
                        width: 100%;
                        right: 0;
            
                    } 
                    &.active::before{
                        content:''; 
                        position: absolute;
                        left: 0; 
                        bottom: -10px; 
                        width: 100%;
                        height: 4px;
                        background-color: #fff;
                        transition: width .2s linear;
                       
                    }
                    // &:hover.menu-link::after{
                    //     width: 100%;
                    //     right: 0;
            
                    // } 
                   }
                    @include from($bp-menu){
                        display: inline !important;
                        padding: 0 !important; 
                        

                    } 
                    color: $color-text-white; 
                    @include to($bp-menu) {
                        color: $color-text-dark2; 
                    }
                      
                }
                
             }  
         } 
    }
} 