// Color de texto
:root{
    --color-body: #F2F2F2;   
    --text-color-white:#F3F3F3;
    --text-color-lighten:#c9c9c9;   
    --text-color-light:#989898;
    --text-color-medium:#666666;
    --text-color:#3F3F3F;
    --text-color-alt:#303030;
    --text-color-accent:#202020;
}
// colores de sucesos
:root{ 
    --bg-primary:#007bff;
    --bg-secondary:#6c757d;
    --bg-success:#28a745;
    --bg-danger:#dc3545;
    --bg-warning:#ffc107;
    --bg-info:#ffc107;
    --bg-light:#f8f9fa;
    --bg-dark:#f8f9fa; 
}
//z-index
:root {
    --z-back: -10;
    --z-normal: 1;
    --z-tooltip: 10;
    --z-fixed: 100;
    --z-modal: 1000;
}
:root{
    --footer-bg: #008E96;
}
// Breakpoint
$bp-menu:50rem;
