}

.success-container .container-success-website .content-link__btn {
  display: inline-block;
  padding: 0.5em 0.9em;
  text-decoration: none;
  border-radius: 18px;
  font-size: 14px;
}

:root {
  --heading-font:  'Roboto','Montserrat', sans-serif ;
  --body-font:  'Roboto','Montserrat', sans-serif ;
  --h1-font-size: rem(24px);
  --h2-font-size: rem(20px);
