/**
  EDgrid es una librería CSS para construir layouts con Responsive Web Design
  Importe este archivo en su proyecto para empezar a usarlo
**/
*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: sans-serif;
  font-size: .9em;
  line-height: 1.5em;
  background-color: #EFEFEF;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

body.dev:before,
body.desarrollo:before, body.dev .ed-container:before,
body.desarrollo .ed-container:before, body.dev .ed-item:before,
body.desarrollo .ed-item:before {
  content: attr(class);
  font-style: italic;
  font-size: 0.75rem;
  font-weight: normal;
  z-index: 1;
  right: 0;
  position: absolute;
  display: table;
  width: 100%;
  padding: 0 0.3125em;
  height: 1.25rem;
  line-height: 1.25rem;
}

.ed-container, .header, .header-main, .main-footer-container, .footer-end__content {
  display: flex;
  flex-wrap: wrap;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.ed-item, .header-main .logo, .header-main .nav {
  margin: 0;
  padding-left: 0.9375em;
  padding-right: 0.9375em;
}

.ed-item {
  width: 100%;
}

.ed-item.ed-container {
  padding-left: 0;
  padding-right: 0;
}

/*
| Mixin para crear cuadriculas
|
| $gridItemSelector: nombre del selector CSS de cada item de la cuadricula
| $listColumns:
|              * lista de numeros de columnas en cada breakpoint (separados por espacios). Ej: 1 2 3 4
|              * el numero de columnas del ultimo breakpoint se hereda a breakpoints superiores
| $gutter: separación entre cada item de la cuadricula (opcional)
|
*/
.button,
button, .button.round,
button.round, .button.radius,
button.radius, .contact-container .contact-content-message__sent {
  display: inline-block;
  padding: .8em 1.6em;
  cursor: pointer;
  border: none;
  text-decoration: none;
}

