:root {
  --color-default:#1c8daf;
  --color-blue:#1874c3;
  --color-green:#0b8457;
  --color-orange:#005585;
  --color-black:#544a6b;
}

.radio {
  display: block;
  position: relative;
  padding-right: 0.2em;
  padding-bottom: 1em;
  font-size: 20px;
  cursor: pointer;
  user-select: none;
}

.radio [class*=checkmark] {
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 25px;
  background-color: var(--color-default);
  border-radius: 50%;
}

.radio .checkmark-primary {
  background-color: var(--color-default) !important;
}

.radio .checkmark-secondary {
  background-color: var(--color-blue) !important;
}

.radio .checkmark-tertiary {
  background-color: var(--color-green) !important;
}

.radio .checkmark-quaternary {
  background-color: var(--color-orange) !important;
}

.radio .checkmark-quinary {
  background-color: var(--color-black) !important;
}

.radio input {
  opacity: 0;
  cursor: pointer;
}

.radio:hover input ~ span[class*=checkmark] {
  background-color: #05556d;
}

.radio input:checked ~ .checkmark-primary {
  background-color: #05556d;
  box-shadow: 0 0 0 2px #ffffff, 0 0 0 4px var(--color-default);
}

.radio input:checked ~ .checkmark-secondary {
  background-color: var(--color-blue);
  box-shadow: 0 0 0 2px #ffffff, 0 0 0 4px var(--color-blue);
}

.radio input:checked ~ .checkmark-tertiary {
  background-color: var(--color-green);
  box-shadow: 0 0 0 2px #ffffff, 0 0 0 4px var(--color-green);
}

.radio input:checked ~ .checkmark-quaternary {
  background-color: var(--color-orange);
  box-shadow: 0 0 0 2px #ffffff, 0 0 0 4px var(--color-orange);
}

.radio input:checked ~ .checkmark-quinary {
  background-color: var(--color-black);
  box-shadow: 0 0 0 2px #ffffff, 0 0 0 4px var(--color-black);
}

.radio input:checked ~ .line-bg:nth-of-type(1) {
  background-color: var(--color-default);
}

.radio span[class*=checkmark]::before {
  content: '';
  position: absolute;
  display: none;
}

.radio input:checked ~ span[class*=checkmark]::before {
  display: block;
}

.radio span[class*=checkmark]::before {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ffffff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.radio .line-bg::before {
  background-color: #0985aa;
}

