//Knowledge
.knowledge{ 
    @include to(78em){
        padding: 0 1em;
    }
    &-title{
         text-align: center;
         color: $color-opacity3;
         font-size: em(15px);
    }
    &-container{  
        @include grid('.knowledge-container-content',2);
        width: 100%;
        @include boxShadow; 
        background-color: #ffffff;
        @include to(45em){
            @include grid('.knowledge-container-content',1,0);
            .knowledge-container-content:nth-child(2){
             margin-top: 0;
            }
            @include to(45em){ 
                &-content:last-child{ 
                   margin-top: 0;
                   padding-top: 0;
                 }
              }
        }
         &-content{  
          padding: 2em 1.5em;  
          .skills{
            display: inline-block;
            margin-bottom: 5px;
            position: relative;
            background-color: #f5f5f5;
            width: 100%;
            height: 25px;
            border-radius: 15px;
            overflow: hidden;
            box-shadow: inset 0 1px 2px rgba(0,0,0,.1); 
            &::before,.p-30::before,.p-35::before,.p-40::before,.p-45::before,.p-50::before,.p-55::before,.p-60::before,.p-65::before,.p-70::before,.p-75::before,.p-80::before,.p-85::before,.p-90::before,.p-95::before{
                width: 50%;  
                content: '';
                position: absolute;
                left: 0; 
                width: 50%;
                height: 100%;
                background: var(--bg-color1); 
                border-radius: 15px; 
            }
            &.p-30::before{ width: 30%; } 
            &.p-35::before{ width: 35%; } 
            &.p-40::before{ width: 40%; } 
            &.p-45::before{ width: 45%; } 
            &.p-50::before{ width: 50%; } 
            &.p-55::before{ width: 55%; } 
            &.p-60::before{ width: 60%; } 
            &.p-65::before{ width: 65%; }
            &.p-70::before{ width: 70%; } 
            &.p-75::before{ width: 75%; } 
            &.p-80::before{ width: 80%; } 
            &.p-85::before{ width: 85%; } 
            &.p-90::before{ width: 90%; }  
            &.p-95::before{ width: 95%; }  
            &__title{
                display: block;
                position: absolute;
                top: 50%;
                transform: translateY(-50%); 
                padding: 5px;
                background-color: rgba(0, 0, 0, 0.25);
                color: #e9e1e1;
                @include to(420px){
                    font-size: 12px;
                }
              }
            &__percent{
                position: absolute;
                display:  block;
                color: rgba(0, 0, 0, 0.40); 
                right: 1%;  
                top: 50%;
                transform: translateY(-50%);  
                @include to(420px){
                    font-size: 12px;
                }
            }
          }
        }
    }
}
 